import React, { Component } from 'react';
import { Tabs, Tab, Grid, Cell, Card, CardTitle, CardActions, CardMenu, CardText } from 'react-mdl';

class Project extends Component {
    constructor(props) {
        super(props);
        this.state = { activeTab: 0 };
    }

    toggleCategories() {
        if (this.state.activeTab === 0) {
            return (
                <div className="projects-grid">
                    {/* // -------------------------PROJECT---1------------------------------------------------------------------------ */}
                    <Card shadow={0} style={{ width: '500px', margin: 'auto' }}>
                        <CardTitle style={{ color: '#fff', height: '155px', background: 'url(data:image/png;base64,) center / cover' }}>Travel-Buddy</CardTitle>
                        <CardText>
                            This app was made for people who love to travel and want an all in one app when it comes to finding tourist spots, restaurants, and lodging all in one type of the city.
                </CardText>
                        <CardActions border>
                            <a href="https://larraineg.github.io/Project1/" target="blank">Link</a>
                        </CardActions>
                        <CardMenu style={{ color: '#fff' }}>

                        </CardMenu>
                    </Card>
                    {/* -------------------------PROJECT---2------------------------------------------------------------------------ */}
                    <Card shadow={0} style={{ width: '500px', margin: 'auto' }}>
                        <CardTitle style={{ color: '#fff', height: '155px', content: 'center', background: 'url(data:image/jpeg;base64,' }}>NOTEPAD--</CardTitle>
                        <CardText>
                            The all in one App designed for people who are researching and taking notes at the go for certain topics which disables the need of opening multiple tabs at the same time
             </CardText>
                        <CardActions border>
                            <a href="https://oscarcat.herokuapp.com/" target="blank">Link</a>
                        </CardActions>
                        <CardMenu style={{ color: '#fff' }}>

                        </CardMenu>
                    </Card>
                    {/* -------------------------PROJECT---3------------------------------------------------------------------------ */}
                    <Card shadow={0} style={{ width: '500px', margin: 'auto' }}>
                        <CardTitle style={{ color: '#00000', height: '155px', content: 'center', background: 'url(data:image/jpeg;base64,' }}>jobHUNT()</CardTitle>
                        <CardText>
                            A job posting App for big corporations and freelancers, a combination of multiple job posting websites, the all in one website for freelances, and corporations.
             </CardText>
                        <CardActions border>
                            <a href="https://github.com/mugen430" target="blank">Link</a>
                        </CardActions>
                        <CardMenu style={{ color: '#fff' }}>

                        </CardMenu>
                    </Card>
                </div>

            )
        } else if (this.state.activeTab === 1) {
            return (
                <Card shadow={0} style={{ width: '500px', margin: 'auto' }}>
                        <CardTitle style={{ color: '#fff', height: '155px', background: 'url(data:image/png;base64,) center / cover' }}>Repository</CardTitle>
                        <CardText>
                            My Github Repo
                </CardText>
                        <CardActions border>
                        <a href="https://github.com/mugen430" target="blank">Link</a>
                        </CardActions>
                        <CardMenu style={{ color: '#fff' }}>

                        </CardMenu>
                    </Card>
            )
        }
    }

    render() {
        return (
            <div className="category-tabs">
                <Tabs activeTab={this.state.activeTab} onChange={(tabID) => this.setState({ activeTab: tabID })} ripple >
                    <Tab>Projects</Tab>
                    <Tab>Repository</Tab>
                </Tabs>

                <Grid>
                    <Cell col={12}>
                        <div className="content">{this.toggleCategories()}</div>
                    </Cell>
                </Grid>
            </div >
        )
    }
}

export default Project;